.post {
  width: 100%;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 16px -8px rgba(0,0,0,0.68);
  box-shadow: 0px 0px 16px -8px rgba(0,0,0,0.68); /* last 2 lines from https://html-css-js.com/css/generator/box-shadow/ */
  margin: 30px 0;
}

.post-wrapper {
  padding: 10px;
}

.post-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.post-top-left {
  display: flex;
  align-items: center;
}

.post-profile-image {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

.post-username {
  font-size: 15px;
  font-weight: 500;
  margin: 0 10px;
}

.post-date {
  font-size: 12px;
}

.post-center {
  margin: 20px 0;
}

.post-image {
  margin-top: 20px;
  width: 100%;
  max-height: 500px;
  object-fit: contain;
}

.post-bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.post-bottom-left {
  display: flex;
  align-items: center;
}

.like-icon {
  width: 24px;
  height: 24px;
  margin-right: 5px;
  cursor: pointer;
}

.post-like-counter {
  font-size: 15px;
}

.post-comment-text {
  cursor: pointer;
  border-bottom: 1px dashed gray;
  font-size: 15px;
} 
